export default{
	props: {
		isShow: {
			type: Boolean,
			default() {
				return false
			}
		}
	},
	data() {
		return {
			isShowMask: this.isShow
		}
	},
	methods: {
		closeMask() {
			this.$emit('closeMask')
		}
	},
	watch: {
		isShow(value) {
			this.isShowMask = value
		}
	}
}
